<template>
  <div class="group">
    <van-form @submit="onSubmit">
      <van-field
        v-model="dataForm.cooperationName"
        name="cooperationName"
        label="单位名称"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="请输入单位名称"
        :rules="[{ required: true, message: '请填写请输入单位名称' }]"
      />
      <van-field
        v-model="dataForm.cooperationSn"
        name="cooperationSn"
        label="单位编号"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="请输入单位编号"
        :rules="[{ required: true, message: '请填写请输入单位编号' }]"
      />

      <van-field
        v-model="dataForm.contact"
        name="contact"
        label="联系人"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="请输入联系人"
        :rules="[{ required: true, message: '请填写联系人' }]"
      />
      <van-field
        v-model="dataForm.phone"
        name="phone"
        label="联系电话"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        maxlength='11'
        placeholder="请输入联系电话"
        :rules="[{ required: true, message: '请填写联系电话' },{ validator:validator, message: '请填写正确的联系电话' }]"
      />
      <van-field
        v-model="dataForm.email"
        name="email"
        label="电子邮箱"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="请输入电子邮箱"
        :rules="[{ validator:validatorEmail, message: '请填写正确格式的邮箱' }]"
      />
      <div
        class="switch"
        style="margin-top:10px;"
        @click="goNext('/account-balance')"
      >
        <div>账户余额</div>
        <div style="display:flex;">
          <div>{{ dataForm.accountAmount }}</div>
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="switch">
        <div>余额支付生活账单</div>
        <van-switch
          active-color="#ff7d41"
          :disabled="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
          v-model="checked"
          size="20px"
        />
      </div>
      <div
        v-if="dataForm.id"
        class="switch"
        style="margin-top:10px;"
        @click="exportForm.email = null,show_export = true"
      >
        <div>生活账单表</div>
        <div style="display:flex;align-items:center;">
          <div>点击导出</div>
          <van-icon name="arrow" />
        </div>
      </div>
      <van-field
        style="margin-top: 12px"
        v-model="cooperDiscount"
        name="cooperDiscount"
        type="number"
        label="折扣"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="请输入折扣"
        :rules="[{ required: true, message: '请填写请输入折扣' }]"
      />
      <van-field
        v-model="dataForm.accountLimit"
        name="accountLimit"
        type="number"
        label="挂账额度"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="请输入挂账额度"
        :rules="[{ required: true, message: '请填写挂账额度' }]"
      />
      <van-field
        readonly
        style="margin-top: 13px;color: #000000"
        label="财务信息"
      />
      <van-field
        v-model="dataForm.bankAccount"
        name="bankAccount"
        label="银行账户"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="选填"
      />
      <van-field
        v-model="dataForm.bankName"
        name="bankName"
        label="开户行"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="选填"
      />
      <van-field
        v-model="dataForm.taxAccount"
        name="taxAccount"
        label="税号"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="选填"
      />
      <van-field
        v-model="dataForm.location"
        name="location"
        label="公司地址"
        :readonly="!dataForm.id || dataForm.id && commitReadonly == false?false:true"
        :input-align="!dataForm.id || dataForm.id && commitReadonly == false?'left':'right'"
        placeholder="选填"
      />

      <div style="margin: 16px;">
        <van-button
          class="submit-btn"
          block
          native-type="submit"
        >
          {{ commitTitle }}
        </van-button>
      </div>
    </van-form>
    <van-button
      v-if="$route.query.accountUnitId"
      @click="removeUnit"
      class="remove-btn"
    >删除协议单位</van-button>
    <van-popup
      v-model="show_export"
      round
      style="width:90%;"
    >
      <div class="f16">导出账单</div>
      <div
        class="flex_box"
        style='align-items:center;'
      >
        <div>选择月份</div>
        <div
          class="box_right"
          @click="show_time = true"
        >
          <div>{{ exportForm.exportDate }}</div>
          <van-icon name="arrow-down" />
        </div>
      </div>
      <div
        class="flex_box"
        style="margin-top:30px;"
      >
        <div>电子邮箱</div>
        <input
          class="box_right"
          style='text-indent:1em;'
          type="email"
          placeholder="请输入邮箱账号"
          v-model="exportForm.email"
        >
      </div>
      <van-button
        class="payedColor"
        @click="confirmExport"
      >确定导出</van-button>
    </van-popup>
    <van-popup
      :close-on-click-overlay='false'
      v-model="show_time"
      style="width:90%;"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        title="选择年月"
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="formatter"
        @confirm="onConfirmTime"
        @cancel="show_time = false"
      />
    </van-popup>
  </div>
</template>
<script>
import Vue from 'vue';
import { Stepper } from 'vant';
import { PostBusinessLifeCostBillCooperExport } from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperationAdd, GetBusinessCooperationEdit, GetBusinessCooperationRemove } from '@/api/api_agreement';
import { GetBusinessCooperation } from '@/api/api_business';
import { Toast, Dialog } from 'vant';
import dayjs from 'dayjs';
import axios from 'axios';
Vue.use(Stepper);
Vue.use(Toast);
export default {
    name: 'add',
    data() {
        return {
            stepper: 1,
            dataForm: {
                cooperationName: null,
                accountAmount: null,
                hotelId: null,
                cooperationSn: null,
                contact: '',
                phone: '',
                cooperDiscount: 1,
                billCycle: '1',
                accountLimit: '',
                bankAccount: '',
                taxAccount: '',
                location: '',
                bankName: '',
                cooperType: '2',
                lifeCost: '1'
            },
            exportForm: {
                cooperId: this.$route.query.accountUnitId,
                cooperName: null,
                exportDate: null,
                email: null
            },
            minDate: null,
            maxDate: null,
            currentDate: new Date(),
            checked: false,
            commitReadonly: true,
            show_export: false,
            show_time: false,
            columns: [],
            columnsAll: [],
            commitTitle: '编辑'
        };
    },
    created() {
        this.commitTitle = this.$route.query.accountUnitId ? '编辑' : '添加';
        this.$store.commit('setHeader', `${this.$route.query.accountUnitId ? '编辑' : '添加'}协议单位`);
        this.dataForm.hotelId = this.$route.query.hotelId;
        this.maxDate = new Date(dayjs());
        this.minDate = new Date(dayjs().subtract(3, 'year'));
        this.exportForm.exportDate = dayjs().format('YYYY-MM');
        if (this.$route.query.accountUnitId) {
            this.dataForm.accountUnitId = this.$route.query.accountUnitId;
            this.getInfoById();
        }
    },
    computed: {
        cooperDiscount: {
            get() {
                return this.dataForm.cooperDiscount * 10;
            },
            set(val) {
                this.dataForm.cooperDiscount = val / 10;
            }
        },
        billCycle: {
            get() {
                if (this.columnsAll && this.columnsAll.length) {
                    return this.columnsAll.find((item) => this.dataForm.billCycle == item.dictValue).dictLabel + ' ' + '＞';
                }
            },
            set(val) {}
        }
    },
    methods: {
        goNext(url) {
            this.$router.push({
                path: url,
                query: {
                    cooperId: this.$route.query.accountUnitId,
                    hotelId: this.dataForm.hotelId,
                    accountAmount: this.dataForm.accountAmount
                }
            });
        },
        getInfoById() {
            GetBusinessCooperation({ cooperationId: this.dataForm.accountUnitId }).done((res) => {
                this.dataForm = { ...this.dataForm, ...res.data };
                this.checked = this.dataForm.lifeCost == '1' ? true : false;
            });
        },
        validator(val) {
            return /^1[3-9][0-9]\d{8}$/.test(val);
        },
        validatorEmail(val) {
            return /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(val);
        },
        // 删除
        removeUnit() {
            Dialog.confirm({
                title: '确定删除?',
                message: this.dataForm.cooperationName
            })
                .then(() => {
                    GetBusinessCooperationRemove({ cooperationId: this.dataForm.accountUnitId }).done((res) => {
                        Toast.success(res.msg);
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 2000);
                    });
                })
                .catch(() => {});
        },
        formatter(type, val) {
            if (type ==
 'year') {
                return `${val}年`;
            } else if (type ==
 'month') {
                return `${val}月`;
            }
            return val;
        },
        onConfirmTime(e) {
            this.show_time = false;
            this.exportForm.exportDate = dayjs(e).format('YYYY-MM');
        },
        confirmExport() {
            this.exportForm.cooperName = this.dataForm.cooperationName;
            this.exportForm.exportDate = this.exportForm.exportDate || dayjs(this.currentDate).format('YYYY-MM');
            // if (!this.exportForm.email || !/^\w+@\w+([-]\w+)*(\.\w+)+$/.test(this.exportForm.email)) {
            //     Toast('请正确输入邮箱账号');
            //     this.exportForm.email = '';
            //     return;
            // }
            if (!this.exportForm.email) {
                axios({
                    method: 'post',
                    url: '/api/business/life/cost/bill/cooper/export',
                    params: this.exportForm,
                    responseType: 'arraybuffer' //注意这个参数必须加，不然会出现下载好的文件打开是乱码情况
                }).then((res) => {
                    const data = res.data;
                    this.show_export = false;
                    const url = window.URL.createObjectURL(new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }));
                    const link = document.createElement('a');
                    link.style.display = 'none';
                    link.href = url;
                    link.setAttribute('download', '生活账单表');
                    //注意：上面的self.$route.query.type是你要下载的文件名加文件后缀，例如test.docx
                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);
                });
            } else {
                PostBusinessLifeCostBillCooperExport(this.exportForm).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                });
                this.show_export = false;
            }
        },
        onSubmit() {
            if (this.commitTitle == '编辑') {
                this.commitTitle = '保存';
                this.commitReadonly = false;
                return;
            }
            this.dataForm.lifeCost = this.checked == true ? '1' : '0';
            if (this.dataForm.accountUnitId) {
                GetBusinessCooperationEdit(this.dataForm).done((res) => {
                    Toast.success(res.msg);
                    setTimeout(() => {
                        this.$router.go(-1);
                    }, 2000);
                });
            } else {
                GetBusinessCooperationAdd(this.dataForm).done((res) => {
                    Toast.success(res.msg);
                    setTimeout(() => {
                        this.$router.go(-1);
                    }, 2000);
                });
            }
        }
    }
};
</script>

<style scoped>
.group {
    position: fixed;
    top: 60px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}

.submit-btn {
    background: #ff7d41;
    border-radius: 8px;
    color: #fff;
}
.remove-btn {
    background: #f6f6f6;
    border: none;
    color: red;
}
.switch {
    display: flex;
    justify-content: space-between;
    padding: 10px 16px;
    font-size: 14px;
    color: #646566;
    background: #fff;
    align-items: center;
}
.f16 {
    font-size: 16px;
    margin: 20px 0;
}
.flex_box {
    display: flex;
    align-items: center;
    padding: 0 20px;
    font-size: 14px;
}
.box_right {
    display: flex;
    padding-left: 70px;
    border: 1px solid #989898;
    padding: 0;
    margin-left: 50px;
    width: 50%;
    padding: 8px 0;
    justify-content: center;
    border-radius: 2px;
}
input::-webkit-input-placeholder {
    font-size: 12px;
}
.payedColor {
    background: #ff7d41;
    width: 90%;
    border-radius: 5px;
    color: #fff;
    margin: 30px 0;
}
/deep/ .van-field__label {
    text-align: left;
}
</style>



